.brekw{overflow: hidden;overflow-wrap: break-word;}
  .qrcode-wrap {
    display: block;
    margin: 5rem auto 1rem;
    width: 40%;
    border: 1px solid #ddd;
    border-radius: 3px;
    overflow: hidden;
    padding: 1rem;
    background: #fff;
  }

  .qrcode-wrap img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }

  .info {
    text-align: center;
    font-size: 1.2rem;
    color: #bbb;
  }

  .ios-btn {
    display: block;
    -webkit-appearance: none;
    border-right: 0 none;
    border-left: 0 none;
    outline: none;
    width: 100%;
    padding: 1rem;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    background: #fff;
    font-size: 1.4rem;
    color: #25AD25;
    text-align: center;
  }

  .ios-btn.ios-btn-link {
    background: none;
    border: 0 none;
  }

  .ios-btn + .ios-btn {
    margin-top: -1px;
  }

  .ios-btn.ios-btn-danger {
    color: red;
    margin-top: 20px;
    text-decoration: underline;
  }

  .ios-btn:disabled {
    color: #bbb;
  }
	.date{    position: absolute;
    margin: auto;
    
    right: 0;
    bottom: 0;
    top: 0;
        height: 1rem;
    }
  .empty-wrap {
    margin-top: 60px;
    text-align: center;
    color: #B3B3B3;
  }

  .empty-wrap .iconfont {
    font-size: 50px;
  }

  .empty-wrap h1 {
    margin-top: 20px;
    font-size: 1.4rem;
  }
  
